import React, {Component} from 'react';
import ImgsViewer from 'react-images-viewer';
import imageSet from './imageSet';
import Blackbox from '../Blackbox/Blackbox';
import ScrollHelper from '../ScrollHelper/ScrollHelper';
import './Gallery.scss';


export default class Gallery extends Component {
  constructor(props) {
    super(props);
    this.state = {
      currImg: 0,
      viewerIsOpen: false,
      galleryIsOpen: false,
      initialImageNumber: 8
    }
  }

  gotoNext = () => {
    this.setState({
      currImg: this.state.currImg + 1
    });
  }

  gotoPrevious = () => {
    this.setState({
      currImg: this.state.currImg - 1
    });
  }

  closeViewer = () => {
    this.setState({
      viewerIsOpen: false
    });
  }

  openViewer = (event, index) => {
    console.log(event.target);
    this.setState({
      currImg: index
    }, this.setState({
      viewerIsOpen: true,
    }));
  }

  openGallery = () => {
    this.setState({
      galleryIsOpen: true
    });
  }

  closeGallery = (event) => {
    console.log(event.target.classList);
    if (!event.target.classList.contains("gallery-thumbnail")) {
      this.setState({
        galleryIsOpen: false
      });
    }
  }
  render () { 
    // const thumbnails = imageSet.map(
    //   (image, index) => <img 
    //     key = {index}
    //     src = {image.src} 
    //     className = "gallery-thumbnail" alt=""
    //     onClick = {(e) => {this.openViewer(e, index)}}
    //     style = {{cursor: "pointer"}}
        
    //   />
    // );

    //Przycisk zobacz więcej żeby zrobić this.setState({imageNumber: 31})
    const thumbnails = [...Array(31).keys()].map(
      (image, index) => <img 
        key = {index}
        src = {require(`./Images/${index + 1}.jpg`)} 
        className = "gallery-thumbnail" alt=""
        onClick = {(e) => {this.openViewer(e, index)}}
        style = {{cursor: "pointer"}}
      />
    );
    return (
      
      <section className = "page-gallery">
        <div className="container">
          <button onClick={this.openGallery}>
            <div className="gallery-button-darken">
              <div className="gallery-mock-button">
                <span>GALERIA</span>
              </div>
            </div>
          </button>
        </div>
        <Blackbox 
          isOn = {this.state.galleryIsOpen ? true : false}
          clickAction = {this.closeGallery}
          height = {"-100vh"}
        >
          
            <div className="page-gallery-thumbails">
              {thumbnails}
            </div>
          
        </Blackbox>
        <ImgsViewer 
          className = "viewer"
          imgs = {imageSet}
          currImg={this.state.currImg}
          isOpen={this.state.viewerIsOpen}
          onClickPrev={this.gotoPrevious}
          onClickNext={this.gotoNext}
          onClose={this.closeViewer}
          backdropCloseable={true}
        />
      <ScrollHelper className="page-footer-scroll" height="100px" />
      </section>
      
    )
  }
}
