import React from 'react';
import './Blackbox.scss';

export default (props) => {
  return (
    <div className="helper-blackbox"
      style = {{
        top: props.isOn ? 0 : props.height,
        opacity: props.isOn ? 1 : 0
      }}
      onClick = {props.clickAction}
    >
      {props.children}
    </div>
  )
}
