import React, {Component} from 'react';
import ScrollHelper from '../ScrollHelper/ScrollHelper';
import Blackbox from '../Blackbox/Blackbox';
import './Info.scss';
import InfoCardShort from './InfoCardShort/InfoCardShort';
import InfoCardLong from './InfoCardLong/InfoCardLong';
import FeelTheReel from './FeelTheReel/FeelTheReel';
import Offer from './Offer/Offer';
import FeelTheReelLong from './FeelTheReelLong/FeelTheReelLong';
import OfferLong from './OfferLong/OfferLong';


export default class Info extends Component {
  constructor() {
    super();
    this.state = {
      isExtendedInfoOn: false,
      isShowInfoOn: false,
      isOfferInfoOn: false,
      areImagesDisplayed: false,
    }
  }
  openExtendedInfo = () => {
    this.setState({
      isExtendedInfoOn: true,
      areImagesDisplayed: true
    });
  }
  closeExtendedInfo = () => {
    this.setState({isExtendedInfoOn: false});
  }

  openShowInfo = () => {
    this.setState({
      isShowInfoOn: true
    });
  }
  closeShowInfo = () => {
    this.setState({
      isShowInfoOn: false
    });
  }
  openOfferInfo = () => {
    this.setState({
      isOfferInfoOn: true
    });
  }
  closeOfferInfo = () => {
    this.setState({
      isOfferInfoOn: false
    });
  }

  render () {

    return (
      <section className = "page-info-card">
        <Blackbox 
          isOn = {this.state.isExtendedInfoOn}
          clickAction = {this.closeExtendedInfo}
          height = "-100vh"
        >
          <div className="container">
            <InfoCardLong isOn = {this.state.areImagesDisplayed ? true : false}/>
          </div>
        </Blackbox>
        <Blackbox
          isOn = {this.state.isShowInfoOn}
          clickAction = {this.closeShowInfo}
          height = "-100vh"
        >
          <div className="container">
            
              <FeelTheReelLong />
          
          </div>
        </Blackbox>
        <Blackbox
          isOn = {this.state.isOfferInfoOn}
          clickAction = {this.closeOfferInfo}
          height = "-100vh"
        >
          <div className="container">
            <OfferLong />
            
          </div>
        </Blackbox>
        <div className="container info-container" >
          <InfoCardShort clickMethod = {this.openExtendedInfo}/>   
          <FeelTheReel clickMethod = {this.openShowInfo}/>
          <Offer clickMethod = {this.openOfferInfo}/>
        </div>
        <ScrollHelper className="page-videos-scroll" height="100px"/>
      </section>
    );
  }
}
